import NewComp from './components/NewComp/NewComp';
import Home from "./components/Home";

import {BrowserRouter as Router, Route, Switch } from 'react-router-dom';



function App() {
  return (
    <Router>
      <div>
        <Switch>
          <Route exact path = 'test-repo/' component = {Home}/>
          <Route  path = 'test-repo/:id' component = {NewComp} />
        </Switch>
      </div>
    </Router>
  );
}


export default App;
